import React, { useState, useEffect } from "react";

import banner_1 from "assets/img/home-1-slider-image-1.jpg";
import banner_2 from "assets/img/home-1-slider-image-2.jpg";
import banner_3 from "assets/img/home-1-slider-image-3.jpg";

import album_1 from "assets/img/album1.jpg";
import album_2 from "assets/img/album2.jpg";
// import album_3 from "assets/img/album3.jpg";

import section_coffee from "assets/img/section_coffee.png";

function HomeComponent(props) {
   const [banner, setBanner] = useState(0);
   const [bannerExample] = useState([
      { url: banner_1, name: "banner_1" },
      { url: banner_2, name: "banner_2" },
      { url: banner_3, name: "banner_3" }
   ]);

   const bannerChange = () => {
      return <img className="banner-full" src={bannerExample[banner].url} alt={bannerExample[banner].name} />;
   };

   useEffect(() => {
      const interval = setInterval(() => {
         const bannerLength = bannerExample.length - 1;
         setBanner(i => (i < bannerLength ? i + 1 : (i = 0)));
      }, 4000);
      return () => {
         clearInterval(interval);
      };
   }, [bannerExample]);

   return (
      <>
         <div className="banner"> {bannerChange()} </div>

         <div className="section">
            <h2 className="text-center">COFFEE BUILD YOUR BASE.</h2>
            <div className="coffee-animation">
               <div className={"section-3-left " + (props.section2Animation ? "animation fadeLeft" : "hidden")}>
                  <div className="section-icon">
                     <div className="section-icon-left">
                        <i className="fas fa-coffee" />
                     </div>
                     <div className="section-icon-right">
                        <h4>Energies</h4>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incid iduntus ut</p>
                     </div>
                  </div>
                  <div className="section-icon">
                     <div className="section-icon-left">
                        <i className="fas fa-coffee" />
                     </div>
                     <div className="section-icon-right">
                        <h4>Energies</h4>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incid iduntus ut</p>
                     </div>
                  </div>
               </div>
               <div className={"section-3-right " + (props.section2Animation ? "animation fadeRight" : "hidden")}>
                  <div className="section-icon">
                     <div className="section-icon-left">
                        <i className="fas fa-coffee" />
                     </div>
                     <div className="section-icon-right">
                        <h4>Energies</h4>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incid iduntus ut</p>
                     </div>
                  </div>
                  <div className="section-icon">
                     <div className="section-icon-left">
                        <i className="fas fa-coffee" />
                     </div>
                     <div className="section-icon-right">
                        <h4>Energies</h4>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incid iduntus ut</p>
                     </div>
                  </div>
               </div>
               <div className={"section-3-middle img-section-2 " + (props.section2Animation ? "animation fadeUp" : "hidden")}>
                  <img src={section_coffee} alt="section coffee" />
               </div>
            </div>
         </div>

         <div className="section">
            <h2 className="text-center">OUR GOOD MEMORIES.</h2>
            <div className="section-album-image-vertical">
               {" "}
               <img src={album_1} alt="album_1" width="100%" />{" "}
            </div>
            <div className="section-album-image-square"> </div>
            <div className="section-album-image-rectangle"> </div>
            <div className="section-album-image-rectangle"> </div>
            <div className="section-album-image-square"> </div>
         </div>

         <div className="section-fluid">
            <div className="parallax"></div>
         </div>
      </>
   );
}

export default HomeComponent;
