import React from "react";
import logo from "assets/img/logo/logo-light.png";
function UserHeader() {
	return (
		<div className="menu">
			<img src={logo} alt="logo-coffee" className="logo-img" />
			<div className="menu-right">
				<ul>
					<li>
						<a className="menu-link" href="/">Home</a>
					</li>
					<li>
						<a className="menu-link" href="#product">Product</a>
					</li>
					<li>
						<a className="menu-link" href="#about">About Us</a>
					</li>
					<li>
						<a href="#cart">
							<i className="fas fa-shopping-cart" /> (0)
                  </a>
					</li>
					<li>
						<a href="#search"><i className="fas fa-search" /></a>
					</li>
					<li>
						<a href="#menu"><i className="fas fa-bars" /></a>
					</li>
				</ul>
			</div>
		</div>
	);
}

export default UserHeader;
