import React, { useState } from "react";
// javascript plugin used to create scrollbars on windows
import { Route, Switch } from "react-router-dom";

import Home from "../Home";
import UserHeader from "components/header/UserHeader.jsx";
import UserFooter from "components/footer/UserFooter";

const Pages = () => {
   const [section2Animation, setSection2Animation] = useState(false);
   window.onscroll = function(e) {
      // called when the window is scrolled.
      let windowYOffset = window.pageYOffset;
      if (windowYOffset > 400 && windowYOffset < 1496 && section2Animation === false) setSection2Animation(true);
   };

   return (
      <div className="container">
         <div className="row">
            <UserHeader />
            <Switch>
               <Route path="/">
                  <Home section2Animation={section2Animation} />
               </Route>
            </Switch>

            <UserFooter />
         </div>
      </div>
   );
};
export default Pages;
